* {
    user-select: none;
    -webkit-tap-highlight-color: transparent;
}

body {
    background-color: #000;
    overflow: hidden;
}

.menu_outer {
    position: fixed;
    width: 600px;
    height: 80px;
    top: calc(100vh - 58px);
    left: calc(50vw - 300px);
    z-index: 2;
    opacity: 0.4;
    transition: all 0.2s;
    color: transparent;
}

.menu_outer:hover, #menu_outer_open {
    opacity: 1;
    top: calc(100vh - 80px);
    color: #FFF;
}

.menu {
    height: 80px;
    background-color: #333;
    border-radius: 20px 20px 0px 0px;
    display: inline-block;
    overflow: hidden;
}

.edit_type {
    width: 80px;
    height: 30px;
    display: inline-block;
    background-repeat: no-repeat;
    background-position: center 18px;
    background-size: 25px;
    line-height: 20px;
    padding-top: 50px;
    font-size: 11px;
    cursor: pointer;
    opacity: 0.75;
    transition: all 0.1s;
    text-align: center;
}

.edit_type:hover {
    background-color: rgba(255,255,255,0.08);
}

#edit_type_open {
    background-color: #06c;
    opacity: 0.95;
}

.switch {
    width: 40px;
    height: 150px;
    position: fixed;
    top: calc(50vh - 70px);
    background-color: #333;
    opacity: 0.4;
    transition: all 0.1s;
    cursor: pointer;
    background-repeat: no-repeat;
    background-size: 30px auto;
    border: none;
}

.switch_r:hover {
    opacity: 1;
    left: calc(100vw - 40px);
}
.switch_l:hover {
    opacity: 1;
    left: 0px;
}

.switch_l {
    left: -20px;
    border-radius: 0px 30px 30px 0px;
    background-image: url("data:image/svg+xml,<svg id='svg' viewBox='0 0 16 16' xmlns='http://www.w3.org/2000/svg' style='transform: rotate(0deg); fill: rgba(255, 255, 255, 1);'><path fill-rule='evenodd' d='M9.224 1.553a.5.5 0 0 1 .223.67L6.56 8l2.888 5.776a.5.5 0 1 1-.894.448l-3-6a.5.5 0 0 1 0-.448l3-6a.5.5 0 0 1 .67-.223z'></path></svg>");
    background-position: 0px 60px;
}


.switch_r {
    left: calc(100vw - 20px);
    border-radius: 30px 0px 0px 30px;
    background-image: url("data:image/svg+xml,<svg id='svg' viewBox='0 0 16 16' xmlns='http://www.w3.org/2000/svg' style='transform: rotate(0deg); fill: rgba(255, 255, 255, 1);'><path fill-rule='evenodd' d='M6.776 1.553a.5.5 0 0 1 .671.223l3 6a.5.5 0 0 1 0 .448l-3 6a.5.5 0 1 1-.894-.448L9.44 8 6.553 2.224a.5.5 0 0 1 .223-.671z'></path></svg>");
    background-position: 10px 60px;
}


.edit_outer {
    position: fixed;
    width: 600px;
    height: 80px;
    top: calc(100vh - 0px);
    left: calc(50vw - 300px);
    z-index: 2;
    opacity: 0.4;
    transition: all 0.1s;
    color: transparent;
}

#edit_outer_open {
    opacity: 1;
    top: calc(100vh - 148px);
    color: #FFF;
}

.edit_item {
    display: inline-block;
    width: 45px;
    height: 45px;
    border-radius: 30px;
    border: 2px solid #FFF;
    margin: 0px 10px;
    vertical-align: top;
    cursor: pointer;
}

.ModelLogo {
    position: fixed;
    z-index: 6;
    width: 300px;
    height: 100px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    top: 20px;
    left: 20px;
}

.Loading {
    width: 100px;
    height: 100px;
    position: fixed;
    z-index: 7;
    background-image: url("../bin/loading.svg");
    background-repeat: no-repeat;
    background-size: 100px;
    top: calc(50vh - 50px);
    left: calc(50vw - 50px);
    opacity: 0.5;
}

@media (orientation: portrait) {
    .switch_r {
        opacity: 1;
        left: calc(100vw - 30px);
        background-position: 1px 60px;
        opacity: 0.3;
    }
    .switch_l {
        opacity: 1;
        left: -10px;
        background-position: 8px 60px;
        opacity: 0.3;
    }

    .switch_r:hover {
        opacity: 0.5;
        left: calc(100vw - 30px);
    }
    .switch_l:hover {
        opacity: .5;
        left: -10px;
    }

    .menu_outer, .menu_outer:hover, #menu_outer_open {
        top: calc(100vh - 135px);
        opacity: 1;
        color: #FFF;
    }
    .edit_type:hover {
        background-color: rgba(213, 85, 85, 0.0);
    }
    #edit_outer_open {
        opacity: 1;
        top: calc(100vh - 205px);
        color: #FFF;
    }

    .menu {
        height: 120px;
        background-color: #333;
        border-radius: 20px 20px 0px 0px;
        display: inline-block;
        overflow: hidden;
    }
    
    .edit_type {
        width: 65px;
        height: 30px;
        display: inline-block;
        background-repeat: no-repeat;
        background-position: center 20px;
        background-size: 22px;
        line-height: 20px;
        padding-top: 50px;
        font-size: 9px;
        cursor: pointer;
        opacity: 0.75;
        transition: all 0.1s;
        text-align: center;
    }

    .ModelLogo {
        position: fixed;
        z-index: 6;
        width: 300px;
        height: 100px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        top: 20px;
        left: calc(50vw - 150px);
    }

    .Loading {
        width: 80px;
        height: 80px;
        position: fixed;
        z-index: 7;
        background-image: url("../bin/loading.svg");
        background-size: 80px;
        top: calc(50vh - 40px);
        left: calc(50vw - 40px);
        opacity: 0.5;
    }
    .arimac_logo {
        top: calc(100vh - 200px) !important;
    }

}